@import '../../styling/variables.scss';

.product-card {
  width: 92%;
  padding: 10px;
  margin: 20px;
  background-color: $dark-blue;
  text-align: center;

  &__name {
    font: 700 14px Arial, Helvetica, sans-serif;
    color: $white;
  }

  &__description {
    font: 400 14px Arial, Helvetica, sans-serif;
    color: $grey1;
  }

  a > * {
    display: block;
    margin: 10px;  
  }
}